import React from "react";
import Chart from "react-apexcharts";

export default function DatalineQuotesApexChartOptionsLazy({ series = [
    { name: 'dataline1', data: [[873057600000, 100], [873057600000, 120]] },
    { name: 'dataline2', data: [[873057600000, 150], [873057600000, 80]] },
] }) {

    const datalineQuotesApexChartOptions = {
        chart: {
            id: 'area-datetime',
            type: 'area',
            height: 350,
            zoom: {
                autoScaleYaxis: true
            }
        },
        dataLabels: {
            enabled: false
        },
        markers: {
            size: 0,
            style: 'hollow',
        },
        xaxis: {
            type: 'datetime',
            //     min: new Date('01 Mar 2012').getTime(),
            tickAmount: 6,
        },
        tooltip: {
            x: {
                format: 'dd MMM yyyy'
            }
        },
        fill: {
            type: 'gradient',
            gradient: {
                shadeIntensity: 1,
                opacityFrom: 0.7,
                opacityTo: 0.9,
                stops: [0, 100]
            }
        }
    }
    return <Chart options={datalineQuotesApexChartOptions} series={series} />
}
